
#mySidenav a {
    position: absolute;
    left: -200px;
    transition: 0.3s;
    padding: 10px;
    width: 250px;
    text-decoration: none;
    font-size: 20px;
    color: white;
    border-radius: 0 5px 5px 0;
    font-family: cursive;
  }
  
  #mySidenav a:hover {
    left: 0;
  }
  
  #home {
    top: 60px;
    background-color: #04AA6D;
    border: solid black;
  }
  
  #stats {
    top: 120px;
    background-color: #2196F3;
    border: solid black;
  }
  
  #milestones {
    top: 180px;
    background-color: #f44336;
    border: solid black;
  }
  
  #nutritionplan {
    top: 240px;
    background-color: rgb(250, 165, 5);
    border: solid black;
  }
  #trainingplan {
    top: 300px;
    background-color: rgb(114, 0, 185);
    border: solid black;
  }
  
  #today_training{
    top: 360px;
    background-color: rgb(60, 0, 0);
    border: solid black;
  }
  
  #contact {
    top: 420px;
    background-color: #555;
    border: solid black;
  }